***********
Ленивая загрузка изображений 
srcImgLQIP - размытое изображение низкого качества
srcImg - само изображение
img.lazyload.mediabox-img(src=srcImgLQIP, data-src=srcImg)

Если это фоновое изображение, то в scss прописать:
background-color: [какой-то цвет];
&.lazyloaded {
    background-image: url($image_dir + 'image-name.png' );
}
***********
font-weight 
100	- Thin (Hairline) - Тонкий (Волосяной) 
200	- Extra Light (Ultra Light) - Дополнительный светлый (Сверхсветлый) 
300	- Light - Светлый
400	- Normal - Нормальный
500	- Medium - Средний
600	- Semi Bold (Demi Bold) - Полужирный
700	- Bold Жирный
800	- Extra Bold (Ultra Bold) - Дополнительный жирный (Сверхжирный) 
900	- Black (Heavy) - Черный (Густой) 
*/
